let current = 0
let cardsCount = $(".review-card").length

function getCurrentCount(){
    if(window.innerWidth>992){
        return 8
    }
    if(window.innerWidth<993 && window.innerWidth > 576){
        return 6
    }

    return 4
}

$("._more-reviews").click(function () {
    current += getCurrentCount()
    if(current + getCurrentCount() >= cardsCount){
        $(this).addClass("_hide")
        $("._hide-reviews").removeClass("_hide")
    }
    for (let i = current + 1; i<current+9;i++){
        let card = $(`.review-card:nth-child(${i})`)
        $(card).addClass("_active")
    }
})

$("._hide-reviews").click(function () {
    $(this).addClass("_hide")
    $("._more-reviews").removeClass("_hide")
    $(".review-card").removeClass("_active")
})

